-->
<template>
  <div class="user-admin">
    <box>
      <!-- 信息筛选 start -->
      <div class="search">
        <div class="search-body">
          <a-form-model
            layout="inline"
            class="search-main"
            ref="searchForm"
            :model="searchForm"
          >
            <a-form-model-item label="用户账号：" prop="userAccount">
              <a-input
                class="default-input"
                v-model="searchForm.userAccount"
                placeholder="请输入"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="用户名称：" prop="name">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选择用户名称"
                v-model="searchForm.name"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  用户名称1
                </a-select-option>
                <a-select-option value="2">
                  用户名称2
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="警员编号：" prop="constableNum">
              <a-input
                class="default-input"
                v-model="searchForm.constableNum"
                placeholder="请输入"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="身份证号：" prop="idNumber">
              <a-input
                class="default-input"
                v-model="searchForm.idNumber"
                placeholder="请输入"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="部门名称：" prop="departmentName">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选择部门名称"
                v-model="searchForm.departmentName"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  部门名称1
                </a-select-option>
                <a-select-option value="2">
                  部门名称2
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="职务：" prop="job">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选择职务"
                v-model="searchForm.job"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  职务1
                </a-select-option>
                <a-select-option value="2">
                  职务2
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="角色：" prop="role">
              <a-input
                class="default-input"
                v-model="searchForm.role"
                placeholder="请输入"
                allow-clear
              />
            </a-form-model-item>
          </a-form-model>
        </div>
      </div>
      <!-- 信息筛选 end -->
      <!-- 编辑导航 start -->
      <div class="operation-nav">
        <div class="btn-box">
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-sousuo1"></span>
            <span class="btn-title">查询</span>
          </a-button>
          <a-button class="operation-button" @click="toReset">
            <span class="iconfont iconfont-18 icon-zhongzhi"></span>
            <span class="btn-title">重置</span>
          </a-button>
          <a-button class="operation-button" @click="addMessage">
            <span class="iconfont iconfont-12 iconfont-16 icon-bianji2"></span>
            <span class="btn-title">新增</span>
          </a-button>
        </div>
      </div>
      <!-- 编辑导航 end -->
      <!-- 表格展示 start -->
      <div class="contents">
        <a-table
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :row-selection="rowSelection"
          :scroll="{ y: 'calc(90vh - 18.5vw)' }"
        >
          <div slot="operation" class="operation">
            <div class="count" @click="resetMessage()">
              <span class="iconfont iconfont-14 icon-zhongzhimima"></span>
              <span>重置密码</span>
            </div>
            <div class="count" @click="editMessage()">
              <span class="iconfont iconfont-18 icon-bianji"></span>
              <span class="title">编辑</span>
            </div>
            <div class="count" @click="getMessage()">
              <span class="iconfont iconfont-18 icon-yanjing"></span>
              <span class="title">查看</span>
            </div>
            <div class="count" @click="deleteMessage()">
              <span class="iconfont iconfont-16 icon-del"></span>
              <span class="title">删除</span>
            </div>
          </div>
        </a-table>
      </div>
      <!-- 表格展示 end -->
      <!-- 分页 start -->
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="100"
          @change="onPaginationChange"
        />
      </div>
      <!-- 分页 end -->
    </box>
    <!-- 重置密码弹窗 start -->
    <resetmodal :visible="resetModal" @close="closeModal"></resetmodal>
    <!-- 重置密码弹窗 end -->
    <!-- 新增弹窗 start -->
    <editmodal
      :title="'新增信息'"
      :visible="addModal"
      @close="closeModal"
    ></editmodal>
    <!-- 新增弹窗 end -->
    <!-- 编辑弹窗 start -->
    <editmodal
      :title="'编辑信息'"
      :visible="editModal"
      @close="closeModal"
    ></editmodal>
    <!-- 编辑弹窗 end -->
    <!-- 查看弹窗 start -->
    <editmodal
      :title="'查看详情'"
      :visible="examineModal"
      @close="closeModal"
    ></editmodal>
    <!-- 查看弹窗 end -->
    <!-- 删除弹窗 start -->
    <deletemodal :visible="deleteModal" @close="closeModal"></deletemodal>
    <!-- 删除弹窗 end -->
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";
import editmodal from "./editModal";
import deletemodal from "../../components/deleteModal";
import resetmodal from "./resetModel";
export default {
  components: {
    Box, //边框组件
    Pagination, //分页组件
    resetmodal, //重置密码弹窗
    editmodal, //新增、编辑、查看弹窗
    deletemodal //删除弹框
  },
  data() {
    return {
      pageSize: 10, // 每页条数
      page: 1, // 当前页
      searchForm: {
        userAccount: "", //用户账号
        name: undefined, //用户名称
        constableNum: "", //警员编号
        idNumber: "", //身份证号
        departmentName: undefined, //部门名称
        job: undefined, //职务
        role: "" //角色
      },
      columns: [
        {
          title: "用户账号",
          dataIndex: "userAccount",
          key: "userAccount",
          width: "4vw"
        },
        {
          title: "用户姓名",
          dataIndex: "name",
          key: "name",
          width: "4vw"
        },
        {
          title: "职务",
          dataIndex: "job",
          key: "job",
          width: "4vw"
        },
        {
          title: "警员编号",
          dataIndex: "constableNum",
          key: "constableNum",
          width: "4vw"
        },
        {
          title: "身份证号",
          dataIndex: "idNumber",
          key: "idNumber",
          width: "10vw"
        },
        {
          title: "角色",
          dataIndex: "role",
          key: "role",
          width: "4vw"
        },
        {
          title: "部门名称",
          dataIndex: "departmentName",
          key: "departmentName",
          width: "5vw"
        },
        {
          title: "创建时间",
          dataIndex: "createTime",
          key: "createTime",
          width: "9vw"
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          width: "14vw",
          scopedSlots: { customRender: "operation" }
        }
      ], //表头信息
      data: [], // 表格内容
      selectedRowKeys: [], // 列表项是否可选择
      resetModal: false, //重置密码弹窗是否显示
      addModal: false, //新增操作弹窗是否显示
      editModal: false, //编辑操作弹窗是否显示
      examineModal: false, //查看操作弹窗是否显示
      deleteModal: false //删除操作弹窗是否显示
    };
  },
  computed: {
    /**
     * 列表项是否可选择
     */
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        selectedRowKeys,
        onChange: this.onSelectChange,
        hideDefaultSelections: true
      };
    }
  },
  mounted() {
    //设置分页数据
    this.$refs.pagination.setData(this.page, this.pageSize);
    // 加载表格数据
    this.setList();
    /**
     * 向列表项追加内容
     */
    // for (let i = 0; i < 20; i++) {
    //   this.data.push({
    //     key: i, //列表key值
    //     userAccount: "admin", //用户账号
    //     name: `张三${i}`, //用户姓名
    //     job: "安检员", //职务
    //     constableNum: "123456", //警员编号
    //     idNumber: "3408811888123455678", //身份证号
    //     role: `角色${i + 1}`, //角色
    //     departmentName: "检查部", //部门名称
    //     createTime: "2021-05-05 12:30:00" //创建时间
    //   });
    // }
  },
  methods: {
    /**
     * 向列表项追加内容
     */
    setList() {
      let data = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          key: i, //列表key值
          userAccount: "admin", //用户账号
          name: `张三${i}`, //用户姓名
          job: "安检员", //职务
          constableNum: "123456", //警员编号
          idNumber: "3408811888123455678", //身份证号
          role: `角色${i + 1}`, //角色
          departmentName: "检查部", //部门名称
          createTime: "2021-05-05 12:30:00" //创建时间
        };
        data.push(item);
      }
      this.data = data;
    },
    /**
     * 点击重置按钮
     */
    toReset() {
      this.$refs.searchForm.resetFields();
    },
    /**
     * 定义浮层的容器，绑定在当前层级上
     */
    getCalendarContainer() {
      return triggerNode => triggerNode.parentNode;
    },
    /**
     * 列表项选中状态
     */
    onSelectChange(selectedRowKeys) {
      console.log("selectedRowKeys changed: ", selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },
    /**
     * 点击×号关闭弹框
     */
    closeModal() {
      this.addModal = false;
      this.editModal = false;
      this.examineModal = false;
      this.deleteModal = false;
      this.resetModal = false;
    },
    /**
     * 点击重置密码打开弹窗
     */
    resetdMessage() {
      this.resetdModal = true;
    },
    /**
     * 点击新增打开弹窗
     */
    addMessage() {
      this.addModal = true;
    },
    /**
     * 点击重置密码
     */
    resetMessage() {
      this.resetModal = true;
    },
    /**
     * 点击编辑打开弹窗
     */
    editMessage() {
      this.editModal = true;
    },
    /**
     * 点击查看打开弹窗
     */
    getMessage() {
      this.examineModal = true;
    },
    /**
     * 点击删除打开弹窗
     */
    deleteMessage() {
      this.deleteModal = true;
    },
    /**
     * 分页事件
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
