<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-20 17:15:26
-->
<template>
  <div class="edit-modal">
    <modal
      width="44.58vw"
      height="30.78vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <a-form-model
        layout="inline"
        class="form-edit"
        ref="ruleForm"
        :model="form"
        :rules="rules"
      >
        <a-form-model-item label="用户账号：" prop="account">
          <a-input
            class="default-input"
            placeholder="请输入用户账号"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="用户姓名：" prop="name">
          <a-input
            class="default-input"
            placeholder="请输入用户姓名"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="职务：" prop="duty">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择职务"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              职务1
            </a-select-option>
            <a-select-option value="2">
              职务2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="警员编号：" prop="policeNumber">
          <a-input
            class="default-input"
            placeholder="请输入警员编号"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="身份证号：" prop="identity">
          <a-input
            class="default-input"
            placeholder="请输入身份证号"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="角色：" prop="role">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择角色"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              角色1
            </a-select-option>
            <a-select-option value="2">
              角色2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="部门：" prop="department">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择部门"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              部门1
            </a-select-option>
            <a-select-option value="2">
              部门2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="创建时间：" prop="createTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')"
          >取消</a-button
        >
        <a-button type="primary" class="form-button save-button" @click="save">保存</a-button>
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      form: {
        name: "",
        type: "",
        signboard: ""
      },
      rules: {
        account: [
          {
            required: true,
            message: "请输入用户账号！",
            trigger: "blur"
          }
        ],
        name: [
          {
            required: true,
            message: "请输入用户姓名！",
            trigger: "blur"
          }
        ],
        duty: [
          {
            required: true,
            message: "请选择职务！",
            trigger: "blur"
          }
        ],
        policeNumber: [
          {
            required: true,
            message: "请输入警员编号！",
            trigger: "blur"
          }
        ],
        identity: [
          {
            required: true,
            message: "请输入身份证号！",
            trigger: "blur"
          }
        ],
        role: [
          {
            required: true,
            message: "请选择角色！",
            trigger: "blur"
          }
        ],
        department: [
          {
            required: true,
            message: "请选择部门！",
            trigger: "blur"
          }
        ],
        createTime: [
          {
            required: true,
            message: "请选择创建时间！",
            trigger: "blur"
          }
        ]
      },
      isShow: this.title == "查看详情"
    };
  },
  methods: {
    save() {
      console.log(this.form);
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less"></style>
